import { useParams } from 'react-router-dom';
import { gql, useQuery } from '@apollo/client';
import _isEmpty from 'lodash.isempty';
import { Alert, useTheme } from '@material-ui/core';
import _get from 'lodash.get';
import Loader from '../../../ui-component/Loader';
import graphqlSchema from '../../../graphql';
import Engine from '../../application/components/FormBuilder/Engine';
import { SURVEYJS_MODE } from '../../application/components/FormBuilder/Constants';

const PropertyFormPage = () => {
	const { propertyId, formId } =
		useParams<{ propertyId: string; formId: string }>();
	const { loading, error, data } = useQuery(
		gql(graphqlSchema.getPropertyFormData),
		{
			variables: { PropertyId: propertyId, FormId: formId },
		}
	);
	const theme = useTheme();
	const propertyForm = _get(data, 'formdata_GetPropertyFormDataItem', {});
	const template = _get(propertyForm, ['Form', 'Template']);
	const formValue = _get(propertyForm, ['FormData', 'DataValue']);
	const formProperty = _get(propertyForm, ['FormProperty']);

	if (loading) return <Loader />;

	if (error && _isEmpty(propertyForm)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Form not found
			</Alert>
		);
	}

	return (
		<div className='container form-view'>
			{!_isEmpty(template) && (
				<Engine
					mode={SURVEYJS_MODE.display}
					formTemplate={template}
					formData={_isEmpty(formValue) ? {} : JSON.parse(formValue)}
					formProperty={_isEmpty(formProperty) ? {} : JSON.parse(formProperty)}
				/>
			)}
		</div>
	);
};

export default PropertyFormPage;
